:root {
  --muj-red: #A41E34;
  --muj-gold: #FFD700;
}

body {
  font-family: 'Inter', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f9fafb;
}

.muj-red {
  color: var(--muj-red);
}

.bg-muj-red {
  background-color: var(--muj-red);
}

.border-muj-red {
  border-color: var(--muj-red);
}

.hover\:bg-muj-red:hover {
  background-color: var(--muj-red);
}

.text-muj-gold {
  color: var(--muj-gold);
}

.bg-muj-gold {
  background-color: var(--muj-gold);
}

.hover\:bg-muj-gold:hover {
  background-color: var(--muj-gold);
}

.facility-card {
  transition: all 0.3s ease;
}

.facility-card:hover {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  transform: translateY(-5px);
}

.nav-link {
  position: relative;
  transition: color 0.3s;
}

.nav-link::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: -5px;
  left: 0;
  background-color: var(--muj-red);
  transition: width 0.3s;
}

.nav-link:hover::after {
  width: 100%;
}

.mobile-menu {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-out;
}

.mobile-menu.active {
  max-height: 500px;
}

.tab-content {
  display: none;
}

.tab-content.active {
  display: block;
}

.tab-button {
  transition: all 0.3s;
}

.tab-button.active {
  border-bottom: 2px solid var(--muj-red);
  color: var(--muj-red);
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

.fade-in {
  animation: fadeIn 0.5s ease-out;
}

@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.4.0/css/all.min.css');
